import {   Box,  Img,  Menu,  MenuButton,  MenuList , Text, useDisclosure } from '@chakra-ui/react'
import React from 'react'
function Menus() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  return (
    <Menu isOpen={isOpen}  >
    <MenuButton
    display={{sm:'inherit',md:'none'}}
      variant="ghost"
      borderRadius={5}
      aria-label="Courses"
      fontWeight="normal"
      onMouseEnter={onOpen}
      onMouseLeave={onClose}
      bg='rgb(243, 14, 79)'
      p={2}
    >
     <Img w={6} src='https://www.clipartmax.com/png/full/36-365828_navbar-toggle-icon-menu-hamburger-png-white.png'/> 
    </MenuButton>
    <MenuList p={4} border='none' onMouseEnter={onOpen} onMouseLeave={onClose}  bg='rgb(243, 14, 79)'>
    
      <Box textAlign={"left"}>
        
        <Text
          p={2}
          fontWeight={600}
          _hover={{
            borderRadius: "5px",
            bg: "gray.100",
            color: "red",
            cursor: "pointer",
          }}
          mx={3}
        >
          Home
        </Text>
        <Text
          p={2}
          fontWeight={600}
          _hover={{
            borderRadius: "5px",
            bg: "gray.100",
            color: "red",
            cursor: "pointer",
          }}
          mx={3}
        >
          About
        </Text>
        <Text
          p={2}
          fontWeight={600}
          _hover={{
            borderRadius: "5px",
            bg: "gray.100",
            color: "red",
            cursor: "pointer",
          }}
          mx={3}
        >
          Skills
        </Text>
        <Text
          p={2}
          fontWeight={600}
          _hover={{
            borderRadius: "5px",
            bg: "gray.100",
            color: "red",
            cursor: "pointer",
          }}
          mx={3}
        >
          Projects
        </Text>
        <Text
          p={2}
          fontWeight={600}
          _hover={{
            borderRadius: "5px",
            bg: "gray.100",
            color: "red",
            cursor: "pointer",
          }}
          mx={3}
        >
          Resume
        </Text>
        <Text
          p={2}
          fontWeight={600}
          _hover={{
            borderRadius: "5px",
            bg: "gray.100",
            color: "red",
            cursor: "pointer",
          }}
          mx={3}
        >
          Contact
        </Text>
       
     
      </Box>
    </MenuList>
  </Menu>
  )
}

export default Menus
